.movie{
    background-color: #070707;
    border-radius: 3px;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.7);
    overflow: hidden;
    margin: 1rem;
    width: 300px;
    position: relative;
    cursor: pointer;
    transition: 0.3s ease-in-out;
}

.movie img{
    object-fit: cover;
    height: 450px;
    max-width: 100%;
    transition: transform 0.5s ease-in-out;
}

.overview{
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    transform: translateY(100%);
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    max-height: 100%;
    padding: 1rem;
    transition: transform 0.5s ease-in-out;
}

.movie:hover .overview{
    transform: translateY(0%);
}

.movie:hover img{
    transform: scale(1.4);
}

.movie:hover{
    box-shadow: 0px 0px 100px rgba(255,255,255,0.9);
}

.vote{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    position: absolute;
    top: 10px;
    right: 10px;
    border: 1px solid white;
}